import styled from 'styled-components'

const RentalContainer = styled.div`
  margin-top: 2rem;
`

const FilterBox = styled.div`
  display: flex;
  justify-content: flex-end;
  margin-bottom: 1rem;

  select {
    padding: 0.5rem 1rem;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
`

const RentalTable = styled.table`
  width: 100%;
  border-collapse: collapse;
  font-size: 14px;

  thead {
    background-color: #f5f5f5;
  }

  th,
  td {
    padding: 12px;
    border-bottom: 1px solid #e0e0e0;
    text-align: left;
  }

  .text-red {
    color: #ef4444;
    font-weight: 500;
  }

  .text-green {
    color: #10b981;
    font-weight: 500;
  }

  .text-gray {
    color: #6b7280;
    font-weight: 500;
  }
`

const CancelButton = styled.button`
  display: inline-block;
  margin-left: 0.5rem;
  background-color: #ef4444;
  color: white;
  padding: 2px 8px;
  font-size: 12px;
  border: none;
  border-radius: 4px;
  cursor: pointer;

  &:hover {
    background-color: #dc2626;
  }
`

const Pagination = styled.div`
  margin-top: 1.5rem;
  display: flex;
  justify-content: center;
  gap: 0.5rem;

  button {
    padding: 6px 12px;
    border: 1px solid #ccc;
    background-color: white;
    border-radius: 4px;
    cursor: pointer;

    &:hover {
      background-color: #e0f2fe;
    }
  }
`

